import {
  Card,
  CardHeader,
  CardBody,
  FormGroup,
  Row,
  Col,
  Input,
  Form,
  Button,
  Label
 
} from 'reactstrap'
import BreadCrumbs from '@components/breadcrumbs'
import Flatpickr from 'react-flatpickr'
import Select from 'react-select'
import { selectThemeColors } from '@utils'
import classnames from 'classnames'
// import { formatDate } from '../../../../utility/Utils'
import React, { Fragment, useEffect, useState } from 'react'
import {  AD_ADD, getPrizeList } from '../../store/actions'
import { useDispatch, useSelector } from 'react-redux'
import { useForm, Controller } from 'react-hook-form'
import { DragDrop } from '@uppy/react'
import '@styles/react/libs/flatpickr/flatpickr.scss'
import Uppy from '@uppy/core'
import '@uppy/core/dist/style.css'
import "./styles.scss"
import './styles copy.scss'
import { getcricketSeries, getfootballSeries } from '../../../banneruploaed/store/action'

// ** Import Scrollbar Paginate Component
import { AsyncPaginate } from "react-select-async-paginate"

const selectstatus = [
  { value: '1', label: 'Video' },
  { value: '2', label: 'Image' }
]

const categoryOptions = [
  {value:'challenge ', label:'Challenge', active:'active'},
  {value:'challenge_info ', label:'Challenge Info ', active:'active'},
  {value:'external_urls', label:'External Urls', url:'url'},
  {value:'redeem', label:'Redeem'},
  {value:'redeem_info', label:'Redeem Info'},
  {value:'refer_friend', label:'Refer Friend'},
  {value:'wallet', label:'Wallet'}
]

const sportscategoryOption = [
  {value:'cricket', label:'Cricket', sportstype:'active' },
  {value:'football', label:'Football' }
] 

const local = new Date()
const datestring = `${local.getFullYear()}-${(local.getMonth()) + 1}-${local.getDate()}`

 const VerticalForm = () => {
  const defaultValues = {
    reactSelect: null
  }

  const store = useSelector(state => state.banner)  
  const store1 = useSelector((state) => state.ads)
  const { register, errors, handleSubmit, control } = useForm({defaultValues})
  const dispatch = useDispatch()
  
  const [AD, setAD] = useState({ sports:"", name:"", playing_time:"", expiry_date: datestring, type:"1", cta:'', asset_url:'', url:'', upload:'', series_id:'', redeem_code:''})
  const [imageUpload, setImageUpload] = useState(null)
  const [imageError] = useState(true)
  const [date_error, setDate_error] = useState(false)
  const [urltext_box, setUrltext_box] = useState(false)
  const [challange_active, setchallange_active] = useState(false)
  const [checksportstype, Setchecksportstype] = useState(false)
  const [category, setCategory] = useState('')
  const [val, setVal] = useState(false)
  const [page, setPage] = useState(1)
  const [value] = useState(null)
  const [currentSporttype, setCurrentSporttype] = useState({
    value: '',
    label: 'Select Category'
  })
  console.log(category.label)
  console.log(imageUpload)
  // console.log(urltext_box, challange_active)

  useEffect(() => {
    dispatch(getPrizeList(page))
  }, [page])


  useEffect(() => {
    dispatch(getcricketSeries())
    dispatch(getfootballSeries())

  }, [])

  const series = store.cricket_name.map(function (ser) {
    
    return {  value: ser.id, label: ser.series_name }
  })

  const [series_id, setSeries_id] = useState({
    value:'',
    label: 'Select Category'
  })

  const sportscategory = (val) => {
    setVal(false)
    setCurrentSporttype(val)
  console.log(val)
    if (val?.sports === 'cricket') {
      Setchecksportstype(true)
    } else {
      Setchecksportstype(false)
    }
  }

  const seriesCategory = (val) => {
    
    setSeries_id(val.value)
    setAD({...AD, series_id:val.value})
  }
  console.log(series_id)

  const footballseries = store.football_name.map(function (ser) {
    return { value: ser.id, label: ser.series_name }
  }) 

  const [footballseries_id, setFootballSeries_id] = useState({
    value:'',
    label: 'Select Category'
  })

  const footballSeriesCategory = (val) => {
   
    setFootballSeries_id(val)
    setAD({...AD, series_id:val.value})
  } 
console.log(footballseries)
  const onSubmit = () => {
    // e.preventDefault()
    setDate_error(true)
    dispatch(AD_ADD(AD))
  }

  const [currentCategory, setCurrentCategory] = useState({
    value: '',
    label: 'Select Category'
  })
  const handleCurrentCategory = (val) => {
    setAD({ ...AD, cta: val.value })
    setCurrentCategory(val)
   
    if (val ? val.active : 'active') {
     
      setchallange_active(true)
    } else {
      setchallange_active(false)
    }

    if (val ? val.url : 'url') {
     
      setUrltext_box(true)
    } else {
      setUrltext_box(false)
    }
   
  }
console.log(footballseries_id)
console.log(currentSporttype.value)
  const uppy = new Uppy({
    meta: { type: 'avatar' },
    autoProceed: true,
    restrictions: { maxNumberOfFiles: 1 }
  })

  uppy.on("file-added", function (file) {
    const reader = new FileReader()
    reader.readAsDataURL(file.data)
    reader.onloadend = function () {
      const base64data = reader.result
      setImageUpload(base64data)
      setAD({...AD, asset_url: base64data})
    }
  })

  const onclear = (e) => {
    e.preventDefault()
    setAD({  name:"", playing_time:"", max_count:"", max_user_count:"", expiry_date:"", type:"", cta:currentCategory.value})
  }

const editcategory = (e) => {   
  setCategory(e)
    setAD({...AD, type:e.value})
  }
const sportscategorySelect = (value) => {
  setAD(value)
  sportscategory(value)
}

const redeemCodeHandler = (e) => {
  setAD({...AD, redeem_code:e.value})
}

console.log(AD.cta)

   // ************************************************************************************* //

   const options = []
   for (let i = 0; i < store1.prizeList?.data?.total; i++) {
     if (page === store1.prizeList?.data?.current_page) {
       options.push({
         value: `${store1.prizeList?.data?.data[i]?.prizes_share_code}`,
         label: `${store1.prizeList?.data?.data[i]?.name}`
       })
     }
     
   }
   const sleep = ms => new Promise(resolve => { setTimeout(() => { resolve() }, ms) })
   const optionsPerPage = 10
   
   const loadOptions = async (search, prevOptions, page) => {
     await sleep(1000)
     if (page === store1.prizeList?.data?.current_page) {
       const nextData = store1.prizeList?.data?.data?.map((nextData) => { return { value: nextData.prizes_share_code, label: nextData.name } })
       const scrollLength = [...prevOptions, ...nextData]
       let filteredOptions
       if (!search) {
         filteredOptions = scrollLength.length > 10 ? scrollLength : options
       } else {
         const searchLower = search.toLowerCase()
         filteredOptions = [...scrollLength.length > 10 ? scrollLength : options].filter(({ label }) => label.toLowerCase().includes(searchLower)
         )
       }
   
       const hasMore = Math.ceil(store1.prizeList?.data?.total / optionsPerPage) > page
       const slicedOptions = filteredOptions.slice(
         (page - 1) * optionsPerPage,
         page * optionsPerPage
       )
   
       return {
         options: slicedOptions,
         hasMore
       }
     }
   }
   const defaultAdditional = {
     page: 1
   }
   
   const loadPageOptions = async (q, prevOptions, { page }) => {
     setPage(page)
     const { options, hasMore } = await loadOptions(q, prevOptions, page)
     return {
       options:[...options],
       hasMore,
   
       additional: {
         page: page + 1
       }
     }
   }
   
    // ************************************************************************************* //

  return (
    <Fragment>
      <BreadCrumbs breadCrumbTitle='Manage AD' breadCrumbParent='Managead' breadCrumbActive='Add' />
    <Card>
      <CardHeader>
      </CardHeader>
      <CardBody>
        <Form onSubmit={handleSubmit(onSubmit)}>
          <Row>
          <Col sm="12" >
              <FormGroup>
                <Label for="nameVertical">Select Sports Type</Label>
                <Controller
              control={control}
              render={({ field: { ...field } }) => (
                <Select
                    {...field}
                    //isClearable={false}
                    theme={selectThemeColors}
                    //className="react-select"
                    classNamePrefix="select"
                    options={sportscategoryOption}
                    className={classnames('react-select', { 'is-invalid': currentSporttype.value === "" && val })}
                    isClearable={true}
                  onChange={(e) => sportscategorySelect({ ...AD, sports: e.value })} 
                  />
                  )}
                  name='reactSelect'
            />
              </FormGroup>
            </Col>
          <Col sm="12">
              <FormGroup>
                <Label for="Name">Name</Label>
                <Input
                  type="name"
                  name="Name"
                  id="Name"
                  innerRef={register({ required: true })}
                  invalid={errors.Name && true}
                  value={AD.name}
                  onChange={(e) => setAD({ ...AD, name: e.target.value })}
                  placeholder="Enter Name "
                />
              </FormGroup>
            </Col>
            <Col sm="12">
              <FormGroup>
                <Label for='digit'>Frequency</Label>
                <Input
                  type="number"
                  name="digit"
                  id="digit"
                  innerRef={register({ required: true })}
                  invalid={errors.digit && true}
                  value={AD.playing_time}
                  onChange={(e) => setAD({ ...AD, playing_time: e.target.value })}
                  placeholder="Enter Frequency"
                />
              </FormGroup>
            </Col>
            <Col sm="12">
              <FormGroup>
                <Label for='digit'>Total AD Count</Label>
                <Input
                  type="number"
                  name="digit"
                  id="digit"
                  innerRef={register({ required: true })}
                  invalid={errors.digit && true}
                  value={AD.max_count}
                  onChange={(e) => setAD({ ...AD, max_count: e.target.value })}
                  placeholder="Enter total ad count"
                />
              </FormGroup>
            </Col>
            <Col sm="12">
              <FormGroup>
                <Label for='digit'>User View Count</Label>
                <Input
                  type="number"
                  name="digit"
                  id="digit"
                  innerRef={register({ required: true })}
                  invalid={errors.digit && true}
                  value={AD.max_user_count}
                  onChange={(e) => setAD({ ...AD, max_user_count: e.target.value })}
                  placeholder="Enter view count per user"
                />
              </FormGroup>
            </Col>
            <Col sm="12" >
              <FormGroup>               
                <Label for="date-time-picker">date Time</Label>
                <Controller
                 control={control}
                 render={({ field: { ...field } }) => (
                   <Flatpickr
                     {...field}                     
                     value={AD?.expiry_date}
                     onChange={(selectedDates, dateStr) => {
                      setAD({ ...AD, expiry_date:dateStr})  
                      setDate_error(false)
                      const firstDate = selectedDates[0]
                      console.log({firstDate, dateStr })
                    }}
                    options = {{      
                      dateFormat: "Y-m-d H:i:ss"
                   }}
                   id='range-picker'
                     className={classnames('form-control', { 'is-invalid':  (AD.expiry_date === "" && date_error) || (AD.date_time === 'NaN-NaN-NaN' && date_error) })}
                     />
                     )}
                     name='reactSelect'
               />
                </FormGroup>
                </Col>
            <Col sm="12" >
              <FormGroup>
                <Label for="nameVertical">Type</Label>
                <Controller
              control={control}
              render={({ field: { ...field } }) => (
                <Select
                  {...field}
                  onChange={(e) => editcategory(e)}
                  defaultValue={''}                 
                  options={selectstatus}
                  theme={selectThemeColors}                  
                  classNamePrefix='select'
                  isClearable={false}
                  />
                  )}
                  name='reactSelect'
            />
              </FormGroup>
            </Col>
            {
                category.label === 'Video' ?
                  <Col sm="12">
                    <FormGroup>
                      <Label className="form-label" for="post_url" >Video Url</Label>
                      <Input
                        type="text"
                        name="post_url"
                        id="post_url"
                        defaultValue={store?.asset_url}
                        value={AD.asset_url}
                        onChange={(e) => setAD({ ...AD, asset_url: e.target.value })}
                        placeholder="Enter Video Url"
                      />
                    </FormGroup>
                  </Col> : ''
              }
            <Col sm="12" >
              <FormGroup>
                <Label for="nameVertical">AD Redirecting</Label>
                <Controller
              control={control}
              render={({ field: { ...field } }) => (
                <Select
                menuPortalTarget={document.querySelector('body')}
                  {...field}
                  onChange={(data) => { handleCurrentCategory(data) }}
                  defaultValue={''}                 
                  options={categoryOptions}
                  theme={selectThemeColors}                  
                  classNamePrefix='select'
                  isClearable={false}
                  />
                  )}
                  name='reactSelect'
            />
              </FormGroup>
            </Col>
            {
              AD.cta === 'redeem_info' ? 
              <Col sm='12'>
              <FormGroup>
            <Label>Prize List</Label>
                <AsyncPaginate
                menuPortalTarget={document.querySelector('body')}
                  theme={selectThemeColors}
                  classNamePrefix='select'
                  additional={defaultAdditional}
                  value={value?.label}
                  loadOptions={loadPageOptions}
                  onChange={(e) => redeemCodeHandler(e)}
                />
            </FormGroup>
            </Col> : ''
            }
            { challange_active ? 
     
     <Col sm='12'>
       {checksportstype ? <FormGroup  >
      <Label>Cricket Series</Label>
      <Select
              menuPortalTarget={document.querySelector('body')}
              isClearable={false}
              theme={selectThemeColors}
              className="react-select"
              classNamePrefix="select"
              options={series}
              value={series.label}
              onChange={(data) => {
                seriesCategory(data)
              }}
              
            />
   
    </FormGroup>  : <FormGroup  >
      <Label>Football Series</Label>
      <Select
              menuPortalTarget={document.querySelector('body')}
              isClearable={false}
              theme={selectThemeColors}
              className="react-select"
              classNamePrefix="select"
              options={footballseries}
              value={footballseries.label}
              onChange={(data) => {
                footballSeriesCategory(data)
              }}
              
            />
   
    </FormGroup>}
       
      </Col>
       
      : ''
      }
            {urltext_box ? <Col>
            <FormGroup>
                    <Label className="form-label" for="post_url" >Screen re-driect</Label>
                      <Input
                        type="url"
                        name="post_url"
                        id="post_url"
                        value = {AD.url}
                        onChange={(e) =>  setAD({...AD, url:e.target.value})}
                        placeholder="Enter Redirecting Screen"
                      />
                    </FormGroup>
            </Col> : ''}
            </Row>
            {
              category?.label === 'Image' ? 
              <Row className="wrapper-add" >
        <Col className="add-post-edit">
         <div className={`mt-4 ${imageError ? 'img-error-post' : ''}`}>
         <DragDrop uppy={uppy}
           locale={{
            strings: {
              dropHereOr: 'Drop file or %{browse}',
              browse: 'browse'
            }
          }}
         />    
                {imageUpload && (
                  <div className="uploaded-files mt-2">
                    <img src={imageUpload} className="post-preview" />
                  </div>
                )}
              </div>
              </Col>
             </Row>  : ''
            }
                 
             <Row className='mt-1'>
            <Col sm="12">
              <FormGroup className="d-flex mb-0">
                <Button.Ripple
                  className="mr-1"
                  color="primary"
                  type="submit"
                  >
                  Submit
                </Button.Ripple>
                <Button.Ripple
                  outline
                  color="secondary"
                  type="reset"
                  onClick={(e) => onclear(e)}
                  >
                  Reset
                </Button.Ripple>
              </FormGroup>
            </Col>
                  </Row>   
        </Form>
      </CardBody>
    </Card>
    </Fragment>
  )
}
export default VerticalForm                